a.youtube:link, a.youtube:visited {
    background-color: var(--bkgButton);
    color: white;
    border: 2px solid black;
    border-radius: 10px;
    padding: 5px 10px;
    text-align: left;
    text-decoration: none;
    text-wrap: wrap;
    display: inline-block;
    width: 250px;
    font-size: 15px;
}
  
a.youtube:hover, a.youtube:active {
    background-color: var(--highlight);
    color: black;
}